form.form {
    margin: 1em auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 800px;
    padding: 1em;
    h3.title {
        font-size: calc(1.3rem + .6vw);
        text-align: center;
        margin-bottom: 1em;
        font-weight: 400;
    }
    input {
        outline: none;
        margin: .5em 0;
        font-size: 1.1em;
        border-radius: 5px;
        border: none;
    }
    .form-group {
        width: 100%;
        label {
            display: block;
            font-size: 1em;
        }
        input{
            border: solid 1px #dee2e65b;
            width: 100%;
            padding: .5em;
            background: none;
            color: #fff;
            &:focus {
                background-color: #fff;
                color: #495057;
                border-color: #80bdff;
                box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .5);
            }
        }
    }
    input[type='submit']{
        padding: .4em 1em;
        color: #fff;
        background-color: #007bff;
        border:solid 3px #007bff;
        border-radius: 5px;

        &:hover {
            background-color: #0069d9;
            border-color: #0062cc;
        }
        &:focus {
            box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .5);
        }
    }
}